.property-description h5 {
    margin: 0px;
    margin-right: 60px;
    font-size: 16px;
    box-shadow: 0px;
    color: black;
    }


.head {
    display: flex;
    flex-direction: row;
    box-shadow: none;
    }

.center {
    margin-left: 70px;
    margin-right: 70px;
    margin-top: 25px;
    display: flex;
    align-items: center;
    }

/* End Non-Essential  */

.property-card {
    height: 10em;
    width: 8em;
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    /* box-shadow:  2px 3px 3px #e1e1e3, -2px -2px 3px #646464; */
    }
/* ^-- The margin bottom is necessary for the drop shadow otherwise it gets clipped in certain cases. */

/* Top Half of card, image. */

.property-image {
    height: 8em;
    width: 10em;
    padding: 1em 1em;
    position: Absolute;
    top: 0px;
    transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
    background-image: url('https://cdn.photographylife.com/wp-content/uploads/2017/01/What-is-landscape-photography.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    }

/* Bottom Card Section */

.property-description {
    background-color: #FAFAFC;
    height: 3em;
    width: 10em;
    position: absolute;
    bottom: 0em;
    transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
    text-align: center;
    }

/* Social Icons */

/* Property Cards Hover States */

.property-card:hover .property-description {
    height: 0em;
    padding: 0px 1em;
    }

.property-card:hover .property-image {
    height: 10em;
    }