.holder {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  overflow: hidden;
}

.about-page {
  width: 50vw;
  display: flex;
  font-family: "Montserrat", sans-serif;
  position: relative;
  animation: mymove 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
}

@keyframes mymove {
  from {
    top: -400px;
  }
  to {
    top: 0px;
  }
}

.about-description {
  width: 50vw;
  min-height: 120vh;
  background-image: url("https://fotofab2.herokuapp.com/static/media/space.f318b581.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50vw 120vh;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  justify-content: space-between;
  position: relative;
  animation: mymove1 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
}

@keyframes mymove1 {
  from {
    top: 110vh;
  }
  to {
    top: 0px;
  }
}

.holder {
  scroll-behavior: smooth;
  margin: auto;
}

.about-devs {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
  padding-top: 20px;
}

.dev-info {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 20px;
}

