/*firstpart*/

.login-photo-container {
  background-image: url("https://bit.ly/3bwHw6v");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 120vh;
  width: 50vw;
}

.info {
  height: 370px;
  width: 100%;
  color: #fff;
  text-align: center;
  text-shadow: 1px 1px 1px #000;
}

.info-para {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
}

.about-description h1 {
  text-shadow: 1px 1px 1px #000;
}

@media (max-width: 700px) {
  .about-description {
    min-width: 100vw;
    min-height: 100vh;
    padding-top: 0;
    background-size: 100vw;
    padding-bottom: 100px;
  }
}

@media (max-width: 700px) {
  .about-page {
    width: 0%;
    height: 0%;
  }
}
