.contact__form input {
    font-size: 10.3333px;
  }

.container1 .contact__wrap {
    text-align: center;
    display: flex;
    flex-direction: column;
    padding-top: 80px;
    width: 60%;
    align-items: center;
    height: 100%;
    background-image: linear-gradient(
      #e7e9ed,
      #e8eaee,
      #e5e7eb,
      #e7e9ed,
      #e0e2e6,
      #dddee2
    );
    position: relative;
    animation: mycontact 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease;
  }

@keyframes mycontact {
  from {
    left: -110vh;
  }
  to {
    left: 0px;
  }
}

// * {
//     box-sizing: border-box;
//     scroll-behavior: smooth;
//     }

.container1 .contact__form textarea {
    height: 120px;
    border: 1px solid grey;
    resize: none;
    border-radius: 25px;
    margin-bottom: 10px;
    outline: none;
    padding: 10px 20px;
  }

.container1 .contact__form input[type="submit"] {
    color: #fff;
    margin-left: 88px;
    margin-top: 20px;
    background: #794a25;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    width: 50%;
    transition: 0.2s;
    }

.container1 .contact__form input {
    height: 33px;
    outline: none;
    border: 1px solid grey;
    border-radius: 25px;
    margin-bottom: 10px;
    transition: 0.2s;
    padding-left: 20px;
    color: rgba(0, 0, 0, 0.712);
    }

.container1 .contact__form {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    width: 50%;
    height: 75%;
    max-width: 400px;
    margin: 0 auto;
    }

.container1 h1 {
    font-size: 36px;
    margin-bottom: 50px;
    color: #000;
    text-shadow: 1px 1px 1px #424242;
  }

.container1 .left {
    width: 40%;
    height: 100%;
    background-image: url(https://images.unsplash.com/photo-1493920670802-3d2a5ced93ee?auto=format&fit=crop&ixid=eyJhcHBfaWQiOjEyMDd9&ixlib=rb-1.2.1&q=80&w=400);
    background-repeat: no-repeat;
    background-size: cover;
    }

// body {
//     display: block;
//     margin: 0px;
// }

.container1 {
    display: flex;
    height: 100vh;
    justify-content: space-between;
  }

.container1 .contact__form input[type="submit"]:hover {
    font-size: 16px;
    border: 0;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  }

@media (max-width: 800px) {
    .container1 {
      flex-direction: column;
    }
}

@media (max-width: 800px) {
    .container1 .contact__wrap {
      width: 100%;
      height: 100%;
      padding: 2px;
      background-image: url(https://images.unsplash.com/photo-1493920670802-3d2a5ced93ee?auto=format&fit=crop&ixid=eyJhcHBfaWQiOjEyMDd9&ixlib=rb-1.2.1&q=80&w=400);
      background-repeat: no-repeat;
      background-size: cover;
    }
}

@media (max-width: 800px) {
    .container1 .contact__wrap h1 {
      padding-top: 100px;
      font-size: 34px;
    }
}

@media (max-width: 800px) {
    .container1 .left {
      width: 0;
      height: 0%;
    }
}
